import { NgModule,Component } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {OrderListComponent} from './order-list/order-list.component';
import {AboutComponent} from './about/about.component';
import { LogoutComponentComponent } from './logout-component/logout-component.component';
import {OrderSaveComponent} from './order-save/order-save.component';
import {OrderCancelComponent} from './order-cancel/order-cancel.component';
import { LoginComponent } from './login/login.component';
import { UserComponent } from './user/user.component';
import { UserOrderComponent } from './user-order/user-order.component';

const routes: Routes = [
  {
    path:"order",component:OrderListComponent
  },
  {
    path:"user/:username",component:UserOrderComponent
  },
  {
    path:"user",component:UserComponent
  },
  {
    path:"about",component:AboutComponent
  },
  {
    path:"",component:LoginComponent
  },
  {
    path:"logout",component:LogoutComponentComponent
  },
  {
    path:"order/save",component:OrderSaveComponent
  },
  {
    path:"order/cancel",component:OrderCancelComponent
  }

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
